export default function (Component) {
Component.options.__tip = "<p><code>fd-table</code> is not yet capable of displaying huge data sets. The current limit is around <code>1.000</code>-<code>5.000</code> rows (depending on the browser, number of elements currently present in the DOM, number of columns, displayed data, …).</p>\n<p><strong>If you want to display a lot of data consider using</strong> <code>fd-virtualized-list</code>.</p>\n"
}