export default "<template>\n  <div>\n    Columns:\n    <fd-button :disabled=\"addDiabled\" @click=\"cols++\" styling=\"light\" icon=\"sys-add\"></fd-button>\n    <fd-button\n      :disabled=\"removeDisabled\"\n      @click=\"cols--\"\n      styling=\"light\"\n      icon=\"sys-minus\"\n    ></fd-button>\n    <br />\n    <fd-container fluid>\n      <fd-col :span=\"1\" v-for=\"col in cols\" :key=\"`col-${col}`\">{{ col }}. Column</fd-col>\n    </fd-container>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return { cols: 6 };\n  },\n  computed: {\n    addDiabled() {\n      return this.cols > 11;\n    },\n    removeDisabled() {\n      return this.cols < 1;\n    }\n  }\n};\n</script>"