export default "<template>\n  <div>\n    <FdFieldSet>\n      <FdLegend>Extra Small Toggles</FdLegend>\n      <FdFormGroup inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"xs\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"xs\" />\n        </fd-form-item-toggle>\n      </FdFormGroup>\n    </FdFieldSet>\n\n    <FdFieldSet>\n      <FdLegend>Small Toggles</FdLegend>\n      <FdFormGroup inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"s\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"s\" />\n        </fd-form-item-toggle>\n      </FdFormGroup>\n    </FdFieldSet>\n\n    <FdFieldSet>\n      <FdLegend>Large Toggles</FdLegend>\n      <FdFormGroup inline>\n        <fd-form-item-toggle label=\"on\">\n          <fd-toggle on size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off\">\n          <fd-toggle size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"on + disabled\">\n          <fd-toggle disabled on size=\"l\" />\n        </fd-form-item-toggle>\n\n        <fd-form-item-toggle label=\"off + disabled\">\n          <fd-toggle disabled size=\"l\" />\n        </fd-form-item-toggle>\n      </FdFormGroup>\n    </FdFieldSet>\n  </div>\n</template>"